<template>
  <div class="card" @click="clickDetailsHandle">
    <my-image class="my-img fade" :imgUrl="data.imgUrl"></my-image>
    <div class="card-content">
      <div class="left">
        <p class="name">{{data.name}}</p>
        <p class="slogan">{{data.slogan}}</p>
      </div>
      <div class="right">
        <mt-button size="small" class="btn buy" type="default">查看详情</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import myImage from "../../lazyloadImg/lazyImage.vue";
import Vue from "vue";
import { Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);
import { mapState } from "vuex";

export default {
  name: "v-card",
  props: {
    data: {
      type: Object
    }
  },
  components: {
    myImage
  },
  created() {},
  methods: {
    clickDetailsHandle(event) {
      this.$store.commit("UPDATE_CURMEALDATA", this.data);
      this.$router.push({ name: "details" });
    }
  },
  computed: {
    ...mapState(["openid", "role", "phoneno", "userid", "token"])
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
.card {
  background: #fefefe;
  padding: 0.1rem 0.1rem 0 0.1rem;
  box-sizing: border-box;

  .my-img {
    width: 100%;
    height: 1.4rem;
    background-color: #f1f1f1;
  }

  .card-content {
    width: 100%;
    min-height: 0.5rem;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      padding: 0.07rem 0;

      .name {
        font-size: 0.14rem;
        font-weight: 600;
        letter-spacing: 0.005rem;
      }

      .slogan {
        margin-top: 0.04rem;
        font-size: 0.13rem;
        line-height: 0.16rem;
        color: #898989;
        letter-spacing: 0.005rem;
      }
    }

    .right {
      margin-top: 0.02rem;

      .btn {
        font-size: 0.12rem;
        height: 0.26rem;
        box-shadow: none;

        &.buy {
          background-color: #ddd;
          color: #595656;
        }
      }
    }
  }
}
</style>



